<script>
import BotUi from "../components/BotUi";
// 对象引入
import {botui} from '../components/BotUi';
import {instance} from '../network/request';

export default {
  name: 'Chatbot',
  components: {
    BotUi,
  },
  data: function () {
    return {
      //用户信息
      age: '',
      agesitems: ['< 25 years old', '25-30 years old', '30-35 years old', '35-40 years old', '40-45 years old', '> 45 years old'],
      gender: '',
      personality: ['', '', '', '', '', '', '', '', '', ''],
      ticksLabels: ['Disagree strongly', 'Disagree a little', 'Neither agree nor disagree', 'Agree a little', 'Agree strongly'],
      personalq: ['is reserved', 'is generally trusting', 'tends to be lazy', 'is relaxed, handles stress well',
        'has few artistic interests', 'is outgoing, sociable', 'tends to find fault with others',
        'does a thorough job', 'gets nervous easily', 'has an active imagination'],
      //采集用户信息dialog
      surveydialog: false,
      //是否设置cookie的弹出框
      cookiedialog: false,
      //屏幕宽度
      screenWidth: document.body.clientWidth + 'px',
      chartSettings: {
        dataName: {'score': 'Points'},
        labelMap: {'score': 'score'},
        seriesMap: {
          'score': {
            min: 0, max: 80,
            axisLine: {
              lineStyle: {
                color: [[0.1, '#33691E'], [0.2, '#689F38'], [0.3, '#9CCC65'], [0.4, '#D4E157'], [0.5, '#FFEE58'], [0.6, '#FBC02D'], [0.7, '#FF9100'], [0.8, '#F4511E'], [0.9, '#D84315'], [1, '#BF360C']],
              }
            },
            axisLabel: {
              textStyle: {
                fontWeight: 'bolder',
                color: '#424242',
                shadowColor: '#EEEEEE',
                shadowBlur: 10
              }
            },
          }
        },
      },
      chartData: {
        columns: ['type', 'value'],
        rows: [
          {type: 'score', value: 0}
        ]
      },
      // ucla结果 跳出的窗口
      scoresheet: false,
      //ucla量表有多少项，默认20项
      items: 20,
      //使用的表单模式，1是chatbot，2是form
      formtype: 1,
      //控制ucla测试的对话框显示
      ucladialog: false,
      //用户输入框内容
      userMessage: '',
      //输入框状态
      msgStatus: true,
      msgStep: 0,
      //回答了第几个问题
      uclastep: 0,
      ucla20questions: [
        'How often do you feel that you are “in tune” with the people around you?',
        'How often do you feel that you lack companionship?',
        'How often do you feel that there is no one you can turn to?',
        'How often do you feel alone?',
        'How often do you feel part of a group of friend?',
        'How often do you feel that you have a lot in common with the people around you?',
        'How often do you feel that you are no longer close to anyone?',
        'How often do you feel that your interests and ideas are not shared by those around you?',
        'How often do you feel outgoing and friendly?',
        'How often do you feel close to people?',
        'How often do you feel left out?',
        'How often do you feel that your relationships with others are not meaningful?',
        'How often do you feel that no one really knows you well?',
        'How often do you feel isolated from others?',
        'How often do you feel you can find companionship when you want it?',
        'How often do you feel that there are people who really understand you?',
        'How often do you feel shy?',
        'How often do you feel that people are around you but not with you?',
        'How often do you feel that there are people you can talk to?',
        'How often do you feel that there are people you can turn to?'
      ],
      //表单回答的结果
      ucla20ans: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
      //表单中用到的问题
      uclaselectquestions: [],
      //ucla3中的用到的问题序号
      ucla3array: [1, 10, 13],
      //ucla10中的用到的问题序号
      ucla10array: [1, 5, 9, 10, 12, 13, 15, 17, 18, 19],
      //反向设计的问题
      uclareversearr: [0, 4, 5, 8, 9, 14, 15, 18, 19],
      //ucla 得分
      uclascore: 0,
      //ucla结果的提示语
      uclatips: '',
      uclaref: '',
      opdquestions: [
        'In general, how would you describe your current mood?',
        'Em…Have you been bothered by low feelings over the past few months?',
        'Can you tell me what happened to make you feel this way?',
        'Can you tell me a little bit about any contact you have with friends or family right now?',
        'Do you consider loneliness to be an issue for you/someone like you?',
        'What do you think could be the main factors that contribute to loneliness?',
        'What would it take for you to feel happier or more at peace?',
        'Think of something, great or small (e.g., food we eat or the place we live in), that you feel happy and grateful for and describe it in a few words.',
        'Do you ever find yourself listening to music to try to influence your mood one way or another?',
        'What do you think of playing music for you when you feel lonely?',
        'What kind of music do you prefer to listen to when you feel lonely?',
        'Would you find it annoying to listen to something upbeat when you are feeling low?',
        'What do you think if I play some songs that you are familiar with when you feel lonely?',
        'What do you think if I play some songs that you are unfamiliar with when you feel lonely?',
        'What do you think of communicating with people who have similar music tastes to you online when you feel lonely? '
      ],
    }
  },
  created: function () {
    switch (this.$route.params.type) {
      case'1':
        this.items = 3;
        this.formtype = 1;
        for (let i = 0; i < this.ucla3array.length; i++) {
          this.uclaselectquestions[i] = this.ucla20questions[this.ucla3array[i]]
        }
        //修改坐标轴
        this.chartSettings.seriesMap.score.min = 3;
        this.chartSettings.seriesMap.score.max = 9;
        this.chartSettings.seriesMap.score.splitNumber = 6;
        break;
      case'2':
        this.items = 3;
        this.formtype = 2;
        for (let i = 0; i < this.ucla3array.length; i++) {
          this.uclaselectquestions[i] = this.ucla20questions[this.ucla3array[i]]
        }
        //修改坐标轴
        this.chartSettings.seriesMap.score.min = 3;
        this.chartSettings.seriesMap.score.max = 9;
        this.chartSettings.seriesMap.score.splitNumber = 6;
        break;
      case'3':
        this.items = 10;
        this.formtype = 1;
        for (let i = 0; i < this.ucla10array.length; i++) {
          this.uclaselectquestions[i] = this.ucla20questions[this.ucla10array[i]]
        }
        //修改坐标轴
        this.chartSettings.seriesMap.score.min = 10;
        this.chartSettings.seriesMap.score.max = 40;
        this.chartSettings.seriesMap.score.splitNumber = 6;
        break;
      case'4':
        this.items = 10;
        this.formtype = 2;
        for (let i = 0; i < this.ucla10array.length; i++) {
          this.uclaselectquestions[i] = this.ucla20questions[this.ucla10array[i]]
        }
        //修改坐标轴
        this.chartSettings.seriesMap.score.min = 10;
        this.chartSettings.seriesMap.score.max = 40;
        this.chartSettings.seriesMap.score.splitNumber = 6;
        break;
      case'5':
        this.items = 20;
        this.formtype = 1;
        this.uclaselectquestions = this.ucla20questions;
        //修改坐标轴
        this.chartSettings.seriesMap.score.min = 20;
        this.chartSettings.seriesMap.score.max = 80;
        this.chartSettings.seriesMap.score.splitNumber = 10;
        break;
      case'6':
        this.items = 20;
        this.formtype = 2;
        //修改坐标轴
        this.uclaselectquestions = this.ucla20questions;
        this.chartSettings.seriesMap.score.min = 20;
        this.chartSettings.seriesMap.score.max = 80;
        this.chartSettings.seriesMap.score.splitNumber = 10;
        break;
      default:
        this.items = 20;
        this.formtype = 1;
        break;
    }
  },
  mounted: function () {
    const that = this
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth + 'px'
      })()
    }
    if (this.$cookies.get('sid')) {
      this.chatstart();
    } else {
      this.cookiedialog = true;
    }
  },
  methods: {
    //回到首页
    goindex: function () {
      this.cookiedialog = false;
      this.$router.replace('/index/').catch((err) => {
        console.log(err.message);
      });
    },
    //开始聊天
    chatstart: function () {
      botui.message.bot({
        loading: true,
        delay: 600,
        content: "Hi there! I am Percy, your virtual assistant."
      }).then(() => {
        botui.message.bot({
          loading: true,
          delay: 500,
          content: "I can help you know yourself better and improve your mood. Can you tell me who you are and what you are studying?",
        });
        this.msgStatus = false;
      });
    },
    //用户发送信息
    msgsend: function () {
      let that = this;
      if (this.$refs.send.validate()) {
        if (that.check_zh(this.userMessage)) {
          alert("Please answer in English");
          return;
        }
        //禁止用户输入
        that.msgStatus = true;
        botui.message.human({
          content: that.userMessage,
        }).then(function () {
          botui.message.bot({
            loading: true,
          }).then((index) => {
            if (that.msgStep === 0) {
              //自我介绍
              instance.post('/self', {
                'id': that.$cookies.get("id"),
                'sid': that.$cookies.get("sid"),
                'intro': that.userMessage,
                'itime': new Date().getTime(),
              }).then((response) => {
                console.log(response.data);
                botui.message.remove(index);
                //继续下一步，心情选择
                that.$refs.send.resetValidation();
                that.userMessage = '';
                that.msgStep += 1;
                that.moodselect();
              }).catch(function (error) {
                that.msgStatus = false;
                console.log(error);
              });
            } else if (that.msgStep > 0 && that.msgStep <= 17) {
              //开放域的问题
              instance.post('/openq', {
                'id': that.$cookies.get("id"),
                'sid': that.$cookies.get("sid"),
                'oitem': 'o' + (that.msgStep - 1),
                'otime': new Date().getTime(),
                'content': that.userMessage,
              }).then((response) => {
                console.log(response.data);
                botui.message.update(index,{
                  loading:false,
                  content:response.data['bot'],
                });
                if (that.msgStep !== 17) {
                    that.openended();
                } else {
                  botui.message.bot({
                    content: "Finished",
                    loading: true,
                    delay: 600,
                  }).then(() => {
                    that.surveydialog = true;
                  })
                }
              }).catch(function (error) {
                that.msgStatus = false;
                console.log(error);
              });
            } else {
              botui.message.remove(index);
              that.surveydialog = true;
              that.userMessage = '';
            }
          })
        })

        console.log("end strp:" + this.msgStep);
      }
    },
    //选择心情
    moodselect: function () {
      let that = this;
      botui.message.bot({
        loading: true,
        delay: 600,
        content: "I hope you enjoy studying at HKBU. I know many students feel low during the pandemic of COVID-19. How are you feeling now?"
      }).then(() => {
        botui.action.button({
          action: [
            {
              text: 'Excited 😀 ',
              value: 'Excited'
            },
            {
              text: 'Happy 😊 ',
              value: 'Happy'
            },
            {
              text: 'Relaxed ☺️ ',
              value: 'Relaxed'
            },
            {
              text: 'Calm 😌️ ',
              value: 'Calm'
            },
            {
              text: 'Sad ☹️ ',
              value: 'Sad'
            },
            {
              text: 'Depressed 😔 ',
              value: 'Depressed'
            },
            {
              text: 'Upset 😭 ',
              value: 'Upset'
            },
            {
              text: 'Nervous 😨 ',
              value: 'Nervous'
            },
          ]
        }).then((res) => {
          console.log(res);
          botui.message.bot({
            loading: true,
          }).then(function (index) {
            instance.post('/mood', {
              'id': that.$cookies.get("id"),
              'sid': that.$cookies.get("sid"),
              'mood': res.value,
              'mtime': new Date().getTime(),
            }).then((response) => {
              console.log(response.data);
              const emotions = ['Excited', 'Happy', '️Relaxed', 'Calm'];
              let msg = 'Sorry to hear that.';
              if (emotions.includes(res.value)) {
                msg = "That is great!";
              }
              botui.message.update(index, {
                content: msg
              }).then(() => {
                //继续下一步，孤独量表
                if (that.$route.params.type === '0') {
                  that.openended();
                } else {
                  that.askloneliness();
                }
              })
            })
          })
        });
      });
    },
    //孤独量表的引导语
    askloneliness: function () {
      let that = this;
      botui.message.bot(
          {
            delay: 600,
            loading: true,
            content: 'Let\'s take a test to learn more about your recent state of mind',
          }
      ).then(() => {
        botui.message.bot(
            {
              delay: 600,
              loading: true,
              content: 'As many students feel they are isolated while working from home. So I want to ask you ' + this.items + ' questions to measure your feeling of loneliness.',
            }
        ).then(() => {
          botui.message.bot({
            delay: 1200,
            loading: true,
          }).then(function (index) {
            botui.message.remove(index);
            switch (that.formtype) {
              case 1:
                that.uclachat();
                break;
              case 2:
                that.uclaform();
                break;
              default:
                that.uclachat();
                break;
            }
          })
        })
      });
    },
    //ucla量表的chatbot形式
    uclachat: function () {
      //items有三个值（3，10，20)
      let that = this;
      let actionbtn = [
        {
          text: 'Never',
          value: 'Never'
        },
        {
          text: 'Rarely',
          value: 'Rarely'
        },
        {
          text: 'Sometimes',
          value: 'Sometimes'
        },
        {
          text: 'Always',
          value: 'Always'
        },
      ];
      if (that.items === 3) {
        actionbtn = [
          {
            text: 'Hardly Ever',
            value: 'Hardly Ever'
          },
          {
            text: 'Some of the Time',
            value: 'Some of the Time'
          },
          {
            text: 'Often',
            value: 'Often'
          },
        ];
      }

      botui.message.bot({
        loading: true,
        delay: 600,
        content: (that.uclastep + 1) + '. ' + that.uclaselectquestions[that.uclastep],
      }).then(function () {
        botui.action.button({
          action: actionbtn,
        }).then((res) => {
          that.getscore(res.text);
          console.log(that.uclascore);
          that.uclastep++;
          if (that.uclastep < that.items) {
            that.uclachat();
          } else {
            botui.message.bot({
              loading: true,
              delay: 600,
              content: 'Thank you for answering all the questions! Let’s see the result of your feeling of loneliness. '
            }).then(() => {
              botui.action.button({
                action: [
                  { // show only one button
                    text: 'Result',
                    value: 'Result'
                  }
                ]
              }).then((res) => {
                console.log(res);
                that.showref();
                that.scoresheet = true;
                that.chartData.rows = [{type: 'score', value: that.uclascore}]
              })
            });
            console.log("finished");
            that.uclastep = 0;
          }
        })
      })
    },
    uclaform: function () {
      //items有三个值（3，10，20)
      this.ucladialog = true;
    },
    getscore: function (item) {
      //将结果传到server
      const item3score = {'Hardly Ever': 1, 'Some of the Time': 2, 'Often': 3};
      const itemscore = {'Never': 1, 'Rarely': 2, 'Sometimes': 3, 'Always': 4};
      const itemrscore = {'Never': 4, 'Rarely': 3, 'Sometimes': 2, 'Always': 1};
      console.log(item);
      let citem = '';
      switch (this.items) {
        case 3:
          citem = 'c' + (this.ucla3array[this.uclastep] + 1);
          this.uclascore += item3score[item]
          break;
        case 10:
          citem = 'c' + (this.ucla10array[this.uclastep] + 1);
          if (this.uclareversearr.includes(this.ucla10array[this.uclastep])) {
            this.uclascore += itemrscore[item]
            console.log(itemrscore[item])
          } else {
            this.uclascore += itemscore[item]
            console.log(itemscore[item])
          }
          break;
        case 20:
          citem = 'c' + (this.uclastep + 1);
          if (this.uclareversearr.includes(this.uclastep)) {
            this.uclascore += itemrscore[item]
            console.log(itemrscore[item])
          } else {
            this.uclascore += itemscore[item]
            console.log(itemscore[item])
          }
          break;
      }
      //在这里上报了选项
      instance.post('/ucla', {
        'id': this.$cookies.get("id"),
        'sid': this.$cookies.get("sid"),
        'citem': citem,
        'ctime': new Date().getTime(),
        'content': item,
      }).then((response) => {
        console.log(response.data);
      });
    },

    closescore: function () {
      this.scoresheet = false;
      botui.message.bot({
        loading: true,
        delay: 600,
        content: "I have get your feeling of loneliness, but I still need to ask more questions to know you better.",
      }).then(() => {
        this.openended();
      });

    },
    openended: function () {
      this.msgStatus = true;
      botui.message.bot({
        loading: true,
        delay: 600,
        content: this.opdquestions[this.msgStep - 1]
      }).then(() => {
        this.msgStatus = false;
        this.$refs.send.resetValidation();
        this.userMessage = '';
      });
      this.msgStep += 1;
    },
    //提交ucla表单
    subucla: function () {
      if (this.$refs.form.validate()) {
        instance.post('/uclaf', {
          'id': this.$cookies.get("id"),
          'sid': this.$cookies.get("sid"),
          'content': this.ucla20ans,
          'ctime': new Date().getTime(),
        }).then((res) => {
          console.log(res.data);
          this.uclascore = res.data['score'];
          this.chartData.rows = [{type: 'score', value: this.uclascore}];
          console.log(this.uclascore);
          this.ucladialog = false;
          this.showref();
          this.scoresheet = true;
        })
        //this.openended();
      } else {
        alert("Some items are missing, please check again.");
      }
    },
    // 显示dish board的提示语
    showref: function () {
      switch (this.items) {
        case 3:
          this.uclaref = '[1]P. Varma, M. Junge, H. Meaklim, and M. L. Jackson, “Younger people are more vulnerable to stress, anxiety and depression during COVID-19 pandemic: A global cross-sectional survey,” Progress in Neuro-Psychopharmacology and Biological Psychiatry, vol. 109, p. 110236, Jul. 2021.';
          if (this.uclascore >= 6) {
            this.uclatips = 'According to a study on loneliness among young people [1], you have stronger feeling of loneliness than 52.8% people have. It should be OK, but you can still talk with your friends and relatives, which may reduce your feeling of loneliness.';
          } else {
            this.uclatips = 'According to a study on loneliness among young people [1], 47.2% people have stronger feeling of loneliness than you have. Keep your good mood and enjoy your life.'
          }
          break;
        case 10:
          this.uclaref = '[1]Nathan M. Hager, Matt R. Judah, Alicia L. Milam et al. Loneliness and Depression in College Students During the COVID-19 Pandemic: Boredom and Repetitive Negative Thinking as Mediators, 03 November 2020.';
          if (this.uclascore >= 20.51) {
            this.uclatips = 'According to a study on loneliness among collage students[1], you have stronger feeling of loneliness than the average. It should be OK, but you can still talk with your friends and relatives, which may reduce your feeling of loneliness.';
          } else {
            this.uclatips = 'According to a study on loneliness among collage students[1], you have less feeling of loneliness than the average. Keep your good mood and enjoy your life.'
          }
          break;
        case 20:
          this.uclaref = '[1]I. F. Tso and S. Park, “Alarming levels of psychiatric symptoms and the role of loneliness during the COVID-19 epidemic: A case study of Hong Kong,” Psychiatry Research, vol. 293, p. 113423, Nov. 2020.';
          if (this.uclascore >= 50) {
            this.uclatips = 'According to a study on loneliness among collage students[1], you have stronger feeling of loneliness than 50% people have. It should be OK, but you can still talk with your friends and relatives, which may reduce your feeling of loneliness.';
          } else {
            this.uclatips = 'According to a study on loneliness among collage students[1], 50% people have stronger feeling of loneliness than you have. Keep your good mood and enjoy your life.'
          }
          break;
        default:
          this.uclatips = '';
          this.uclaref = '';
      }
    },
    subuserinfo: function () {
      if (this.$refs.personalinfo.validate()) {
        instance.post('/person', {
          'id': this.$cookies.get("id"),
          'sid': this.$cookies.get("sid"),
          'age': this.age,
          'gender': this.gender,
          'etime': new Date().getTime(),
          'personality': this.personality,
        }).then((response) => {
          console.log(response.data);
          this.surveydialog = false;
          this.$router.replace('/about').catch((err) => {
            console.log(err.message);
          });
        });
      } else {
        alert("Some items are missing, please check again.");
      }
    },
    check_zh: function (str) {
      const reg = /[\u4E00-\u9FA5\uF900-\uFA2D]/;
      return reg.test(str);
    },
    randomans: function () {
      const ans = ['Thanks for letting me know.', 'Okay', "I understand", 'Ok.', 'Got it.',
        'I see.', 'Thank you for your thoughtful input.', '❤️', '👌', 'Thank you very much for sharing.'];
      return botui.message.bot({
        content: ans[Math.floor(Math.random() * 10)],
        delay: 600,
        loading: true,
      })
    }
  }
}
</script>

